html, body, #root {
    width: 100%;
    height: 100%;
    font-family: sans-serif;
}

main {
    margin: 10px;
    height: calc(100vh - 60px);
    padding-top: 30px;
}

menu {
    z-index: 10;
    position: absolute;
    margin: 0;
    padding: 0

}


.loader {
    position: relative;
    top:50%;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #253545;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin: auto;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.wrapper{
    display:grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: "nav main";
}

nav{
    grid-area: nav ;
}

main{
    grid-area: main;
}
